.header{
    background-color: #2A71B1;
&__language{
select{
    background-color: #fff;
    border: none;
  
}
}
    &__menu{
        position: relative;
    }
    &__menuList{
        display: flex;
        justify-content: end;
        align-items: center;
        margin-top: 12px;

        li{
            margin-left: 20px;
            list-style-type: none;
            a{
                text-decoration: none;  
                color: #fff;     
                padding: 5px;
                border-radius: 5px;
                transition: ease-in .2s;

                &:hover{
                    background-color: #fff;
                    color: #2A71B1;
                }
            }
        }
    }

&__bars{
    display: none;
}
    &__logo{
        img{
        width: 70px;

}
    }
}          


@media only screen and (max-width: 768px) {
   .header__menu{
    display: none;
   } 

   .header{

    &__language{
        display: none;
    }
    &__bars{
        display: block;
        color: #fff;
        ul li{
            list-style-type: none;

            i{
             position: absolute;
             top: 25px;
             right: 20px;
             font-size: 22px;
             &:hover{
                cursor: pointer;
             }
            }
        }
    }
     
    } 

    .firstBlock__socialMedia  {
        padding: 6px;
      a{
        img{
            max-width: 33px;
            padding-top: 10px;
           }
      }
    }
   }


   
